<template>
    <v-container>
        <v-row>
            <v-col>
                <v-card>
                    <v-card-title>管理员面板：excel文件上传</v-card-title>
                    <v-card-subtitle style="margin-top: 1vh;">欢迎，管理员{{supervisor}}!</v-card-subtitle>

                    <!--          管理面板切换-->
                    <v-tabs
                            fixed-tabs
                            background-color="indigo"
                            dark
                    >
                        <v-spacer></v-spacer>
                        <v-tab to='/universityTable'>
                            院校信息管理
                        </v-tab>
                        <v-tab  to='/layout/TestPage'>
                            学生账户管理
                        </v-tab>
                        <v-tab to='/teacherTable'>
                            教师账户管理
                        </v-tab>
                        <v-tab to='/adminTable'>
                            管理员账户管理
                        </v-tab>
                      <v-tab to='/uploadTable'>
                        Excel文件上传
                      </v-tab>
                    </v-tabs>

                    <v-btn class="blue white--text" style="margin-top: 1vh; margin-bottom: 1vh; width: 100%">欢迎！</v-btn>
                </v-card>


            </v-col>

        </v-row>




        <v-file-input
                accept=".xlsx"
                placeholder="点击此处上传您的excel文件"
                show-size
                prepend-icon="mdi-paperclip"
                style="width: 70%"
                v-model="file"
        >
            <template v-slot:selection="{ text }">
                <v-chip
                        small
                        label
                        color="primary"
                >
                    {{ text }}
                </v-chip>
            </template>
        </v-file-input>
        <v-card-actions>
        <v-btn
                color="blue darken-1"
                text
                @click="uploadExcel"
        >
            Save
        </v-btn>

            <v-spacer></v-spacer>
            <v-btn
                    color="blue darken-1"
                    text

            >
                Cancel
            </v-btn>


        </v-card-actions>



    </v-container>
</template>
<script>
    import axios from "axios";

    export default {
        name: 'Contacts',
        data: () => ({
            file:[],
            snack:true,
            //界面数据
            supervisor:'',

            stdGet:'',
            stdUpdate:'',
            stdDelete:'',
            stdRegister:'',

            selectPage:'',
            choosedTitle:'excel文件上传',

            //账号<20 8<=密码<20
            dialog: false,
            dialogDelete: false,
            headers: [
                {
                    text: '招生代码',
                    align: 'start',
                    sortable: false,
                    value: 'collegeID',
                },
                { text: '名称', value: 'name' },
                { text: '投档线分数', value: 'score' },
                { text: '批次', value: 'tier' },
                { text: '投档线排名', value: 'crank' },
                { text: '种类', value: 'kind' },
                { text: '修改', value: 'actions', sortable: false },
            ],
            stdData: [],
            editedIndex: -1,
            editedItem: {
                collegeID: '',
                name: '',
                tier: '',
                score: '',
                crank:'',
                kind:'',
            },
            defaultItem: {
                collegeID: '',
                crank: '',
                name: '',
                score: '',
                tier: '',
                kind:'',
            },

            test_url:"",//测试的url
            test_var1:"",//测试的var1
            test_var2:"",//测试的var2

        }),
        mounted() {
        },
        computed: {
            formTitle () {
                return this.editedIndex === -1 ? '新建' : '编辑'
            },
        },

        watch: {
            dialog (val) {
                val || this.close()
            },
            dialogDelete (val) {
                val || this.closeDelete()
            },
        },

        created () {
            this.initialize()
        },

        methods: {
            uploadExcel: function () {
                var that = this;
                var url = 'http://127.0.0.1:9090/excel/upload'
                const param = new FormData();
                // processData: false, // 告诉axios不要去处理发送的数据(重要参数)
                    // contentType: false, // 告诉axios不要去设置Content-Type请求头
                param.append('file', that.file);
                // param.append('pictureID', that.picName)
                const config = {
                    headers: {'Content-Type': 'multipart/form-data;boundary = ' + new Date().getTime()}
                };
                alert("发excel包"+this.file)
                axios.post(url, param, config)
                    .then(function (res){
                        if(res.data.code==="0"){
                            alert("成功上传")
                        }
                    }).catch(function(){
                    alert("出现错误，请检查您的文件");
                }
                )
            },

            initialize () {

            },


        },
    }
</script>
